import { weights } from "./weights";

export let predict = (k, psi, th, sv) => {
  console.log(k, psi, th, sv);
  k = (k - weights[0].k_mean) / weights[1].k_std;
  psi = (psi - weights[0].psi_mean) / weights[1].psi_std;
  th = (th - weights[0].th_mean) / weights[1].th_std;
  sv = (sv - weights[0].sv_mean) / weights[1].sv_std;

  let result = +weights[2].intercept;

  for (var i = 0; i < weights[3].length; i++) {
    result +=
      weights[3][i].Weight *
      k ** weights[3][i].K *
      psi ** weights[3][i].PSI *
      th ** weights[3][i].TH *
      sv ** weights[3][i].SV;
  }

  console.log(result);
};

export let linear_predict = (k, psi, th, sv) => {
  let result =
    k * 0.03705807936 +
    -0.004238967074 * psi +
    -4.622968134 * th +
    -0.05907749263 * sv +
    67.9435;
  return result;
};
