import React, { useState } from "react";

import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";

import { linear_predict } from "./Predictor";

const useStyles = makeStyles(theme => ({
  "@global": {
    body: {
      backgroundColor: theme.palette.common.white
    }
  },
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  }
}));

export default function Dashboard() {
  const classes = useStyles();

  const [k, setK] = useState("");
  const [psi, setPSI] = useState("");
  const [th, setTH] = useState("");
  const [sv, setSV] = useState("");
  const [prediction, setPrediction] = useState("");

  let onPredict = () => {
    console.log("on predict called");
    if (k == "" || psi == "" || th == "" || sv == "") {
      setPrediction(`Invalid input`);
      return 0;
    } else {
      let value = linear_predict(k, psi, th, sv);
      setPrediction(`Predicted wobbe : ${value}`);
    }
  };

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Typography component="h1" variant="h5">
          Model{" "}
        </Typography>
        <form className={classes.form} noValidate>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="p"
            label="P"
            type="number"
            name="p"
            autoComplete="p"
            autoFocus
            onChange={event => setK(event.target.value)}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="psi"
            label="PSI"
            type="number"
            id="psi"
            autoComplete="psi"
            onChange={event => setPSI(event.target.value)}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="th"
            label="Th"
            type="number"
            id="th"
            autoComplete="th"
            onChange={event => setTH(event.target.value)}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="sv"
            label="SV"
            type="number"
            id="sv"
            autoComplete="sv"
            onChange={event => setSV(event.target.value)}
          />

          <Button
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            //onClick={() => predict(253.15,17.7,0.0274517034,401.7452690456)}
            onClick={() => onPredict()}
          >
            Predict
          </Button>
          {prediction}
        </form>
      </div>
    </Container>
  );
}
